import React from "react";
import "../UI/Card.css";
import { ImLocation } from "react-icons/im";

export default function Card(props) {
  return (
    <div>
      <div className="card">
        <div className="card--img-container">
          <img
            className="card--img-container__img"
            src={props.imageUrl}
            alt="travel-pic"
          />
        </div>

        <div className="card--content">
          <div className="card--content__location-detail">
            <span className="card--location__icon">
              <ImLocation />
            </span>
            <h4 className="card--location__country">{props.location}</h4>
            <a className="card--location__map" href={props.googleMapsUrl}>
              View on Google Maps
            </a>
          </div>
          <h1 className="card--content__heading">{props.title}</h1>
          <h3 className="card--content__date">
            {props.startDate} - {props.endDate}
          </h3>
          <p className="card--content__desc">{props.description}</p>
        </div>
      </div>

      <hr />
    </div>
  );
}
